export default {
  mounted(el, binding) {
    const options = { rootMargin: '0px', threshold: 1.0 }
    const callback = entries => {
      setTimeout(() => {
        if (entries[0].isIntersecting) {
          console.log('Crossed')
          binding.value()
        }
      }, 500)
    }
    const observer = new IntersectionObserver(callback, options)
    observer.observe(el)
  },
  name: 'intersection',
}
